
import { defineComponent } from 'vue';

export default defineComponent({
    name: "ChatMessage",
    props: {
        textContent: String,
        authorName: String,
        timestamp: String
    },
    methods: {
        formatDateTime(str?: string) : string{
            let arr = str?.split("_");
            if(!arr){ return ""}
            return arr[3] + ":" + arr[4] + ":" + arr[5] + " " + arr[2] + "/" + arr[1] + "/" + arr[0];
        }
    }
})
